export default {
  name: "Home",
  data: function data() {
    return {
      switchTitle: true,
      message: ""
    };
  },
  methods: {
    fadeTitle: function fadeTitle() {
      setTimeout(function () {
        this.switchTitle = false;
      }.bind(this), 4000);

      if (this.switchTitle === true) {
        return this.message = "初めてVue.jsを用いてポートフォリオを作ってみました！";
      } else {
        return this.message = "出来はイマイチですが、ゆっくりと見ていってください！";
      }
    }
  }
};